@media (max-width: 750px) {
   .intro {
      flex-direction: column !important;
      align-items: baseline !important;
      justify-content: center !important;
   }

   .intro > div {
      flex: none;
   }
}

.intro {
   display: flex;
   align-items: center;
   flex-direction: row;

   height: 100vh;
}

.intro-box {
   flex: 1;
   justify-self: left;
}

.intro-title {
   font-size: 32px;
}
.intro-title p:nth-child(2) {
   font-weight: bold;
   color: royalblue;
}

.intro-links {
   object-fit: cover;
   margin-top: 10px;
}

.intro-link-label {
   height: 15px;
}

.intro-link {
   width: 32px;
   filter: invert(var(--invert));
   padding: 3px;

   transition: opacity 0.1s ease-out;
}

.intro-link:hover {
   opacity: 0.8;
   cursor: pointer;
}

.intro-duck {
   flex: 1;
   justify-self: right;
}

.intro-duck pre {
   text-align: right;
}

.intro-duck .water {
   color: royalblue !important;
}

.intro-duck .beak {
   color: orange !important;
}